import {uniPopup} from "@dcloudio/uni-ui/lib/uni-popup/uni-popup.vue" 
export default {
	data() {  
		return {
		details:[],
		id:"",
		branch:"",
		mark:'',
		part_name:'',
		collection:1,
		pic:[],
		tube:'',
		com_mark:999,
		checkTitle:false,
		upper_id:'',
		Id:'',
		tradeId:'',
		}
	},
	onLoad:function(option){ 
		var _self = this
		_self.id = option.id
		_self.branch = option.branch
		_self.mark = option.mark
		_self.part_name = option.part_name
		_self.tradeId = uni.getStorageSync('tradeId')
		_self.tube = uni.getStorageSync('tube') 
		_self.com_mark = uni.getStorageSync('mark')
		_self.upper_id = option.upper_id
		_self.Id = option.Id
		console.log(option)
		
		console.log(_self.tradeId)
		console.log(_self.Id)
		console.log(_self.com_mark)
		console.log(_self.id)
		console.log(option)
		_self.getInfo()
		_self.checkAuth()
	},
	onShow:function(){
		var _self  = this
		_self.getInfo()
		_self.checkAuth()
	},
	methods: {
		/**
		 * 判断是否验证
		 */
		checkAuth:function(){
			var _self = this
			uni.request({ 
				url:_self.$api+"dockingManager/judgeCard",
				data:{optionId:uni.getStorageSync("openId")},
				method:"GET",
				success:function(res){
					console.log(res)
					if(res.data==0){
						_self.checkTitle = false
					}
					if(res.data==1){
						_self.checkTitle = true
					}
				}
			})
		},
		getInfo:function(){
			var _self = this
			uni.request({ 
				url:_self.$api+"dockingManager/declareNewQuery",
				data:{id:_self.id,optionId:uni.getStorageSync("openId"),branch:_self.branch,mark:_self.mark},
				method:"GET",
				success:function(res){
					console.log(res)
					//用户mark为undefined该用户未收藏
					if(res.data[0].state === undefined || res.data[0].state===null){
						_self.collection = 1
					}else{
						_self.collection = 2
					} 
					_self.details = res.data[0]
					
					if(res.data[0].shrink==""){
						_self.pic= []
					}else{
						_self.pic = res.data[0].shrink.split(',')
					}
					console.log(res)
				}
			})
		},
		previewImage1: function(e) {
			console.log(e)
			uni.previewImage({
				current: e,
				urls: [e]
			})
		},
		previewImage: function(e) {
			uni.previewImage({
				current: e,
				urls: this.pic
			})
		},
		/**
		 * @param {Object} options
		 * 删除
		 */
		deleteContent:function(){
			  var _self = this
			  uni.showModal({
							title: '提示',
							content: '是否删除此内容',
							success: function (res) {
									if (res.confirm) {
										uni.request({
											url:_self.$api+'dockingManager/titleDeclareDelete',
											data:{id:_self.id},
														success:function(res){
															if(res.data==1){
																uni.showToast({
																	title:'删除成功'
																})
																uni.navigateBack({
																	delta: 1,
																	animationType: 'pop-out',
																	animationDuration: 200
																});
																return
															}else{
																uni.showToast({
																	title:'删除失败',
																	icon:'none'
																})
																return
															}
														}
										})
									} else if (res.cancel) {
										console.log('用户点击取消');
									}
								}
							});  
		},
		/**
		 * @param {Object} options
		 * 修改
		 */
		editContent:function(){
			 var _self = this
			 uni.navigateTo({
			 	url:'../productDetailEdit/productDetailEdit?id='+_self.id+'&branch='+_self.branch+'&mark='+_self.mark
			 })
		},
		/**
		 * 转发
		 */
		onShareAppMessage: function (options) {
				var _self = this
				if (options.from === 'button') {
				  // 来自页面内转发按钮
				  console.log(options.target)
				}
				return {
				  //## 此为转发页面所显示的标题
				  //title: '好友代付', 
				  //## 此为转发页面的描述性文字
				  desc: _self.details.name, 
				  //## 此为转发给微信好友或微信群后，对方点击后进入的页面链接，可以根据自己的需求添加参数
				  path:  'pages/pageChildren/productDetail/productDetail?id='+_self.id+'&branch='+_self.branch+'&optionId='+uni.getStorageSync("openId")+'&mark='+mark,
				  //## 转发操作成功后的回调函数，用于对发起者的提示语句或其他逻辑处理
				  success: function(res) {
					  uni.request({
					  url:_self.$api+"dockingManager/totalHideAdd",
					  data:{id:_self.id,mark:1,optionId:uni.getStorageSync("openId")},
					  success:function(res){
						  console.log(res)
						  	console.log("转发成功")
					  },
					  }) 
				  },
				  //## 转发操作失败/取消 后的回调处理，一般是个提示语句即可
				  fail: function() {
					  console.log("error")
				  }
				}
		},
		//拨打电话
		apply:function(){
			var _self = this
			if(_self.checkTitle){
				uni.makePhoneCall({
				phoneNumber: _self.details.phone //仅为示例
				});
			}else{
				uni.showToast({
					title:"您尚未进行认证，请先进行认证",
					icon:"none"
				})
				return false
			}
		},
		/**
		 * 收藏
		 * 
		 */
		collect:function(){
			var _self = this
			if(this.collection == 1){
				uni.request({
					url:_self.$api+"dockingManager/hideAdd",
					data:{id:_self.id,mark:0,optionId:uni.getStorageSync("openId")},
					success:function(res){
						console.log(res)
						if(res.data==1){
							console.log("收藏成功")
							_self.collection = 2
						}
						else if(res.data==99){
							uni.showModal({
							title: '提示',
							content: '此操作需用户授权，是否进行授权',
							success: function (res) {
								if (res.confirm) {
									//跳转到授权页面  
									uni.navigateTo({
										url:"../../login/login"  
									})
									console.log('用户点击确定');
								} else if (res.cancel) {
									console.log('用户点击取消');
								}
							}
						});
						}
						else if(res.data ==98){
								uni.showToast({
								title:"您尚未进行认证，请先进行认证",
								icon:"none"
							})
							return false
						}
						else{
							uni.showToast({
								title:"收藏失败",
								icon:"none"
							})
						} 
					}
				})
			}else{
					uni.request({
					url:_self.$api+"dockingManager/hideDelete",
					data:{id:_self.id,optionId:uni.getStorageSync("openId")},
					success:function(res){
						console.log(res)
						if(res.data==1){
							console.log("取消收藏成功")
							_self.collection = 1 
						}else if(res.data==99){
							uni.showModal({
								title: '提示',
								content: '此操作需用户授权，是否进行授权',
								success: function (res) {
									if (res.confirm) {
										//跳转到授权页面  
										uni.navigateTo({
											url:"../login/login"  
										})
										console.log('用户点击确定');
									} else if (res.cancel) {
										console.log('用户点击取消');
									}
								}
							});
						}
						else{
							uni.showToast({
								title:"取消收藏失败",
								icon:"none"
							})
						}						
					},
					})
			}
		},
	},
	 components: {uniPopup},
	
}
